@font-face {
  font-family: bangers;
  src: url(fonts/bangers.regular.ttf);
}
@font-face {
  font-family: alex;
  src: url(fonts/AlexBrush-Regular.ttf);
}

$color: steelblue;

.main-view {
  background-color: $color;
}
.navbar > * {
  color:#4682b4;
  font-size: 28px;
  font-weight: 800;
  font-family: alex;
  }  
.nav-items {
  margin-right: 20px;
  font-size: 26px;
  text-decoration: none;
}
.profile-link {
  text-decoration: none;
}
.logout-button {
  font-size: 24px;
}
h1 {
  padding: 20px 0 0 0;
  font-size: 50px;
  line-height: 1.6rem;
  font-family: bangers;
  color: black;
}
.brand {
  text-decoration: none;

}
.card {
  height: 100%;
  margin: 0 auto;
}
.card-info {
  font-size: 18px;
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
  font-weight: 400;
}
.card-title {
  font-size: 35px;
  font-weight: 800;
  font-family: bangers;
}
.open-button {
  background-color: rgb(43, 101, 226);
  font-weight: 800;
  font-family: bangers;
}
.back-button {
  margin-top: 20px;
  font-family: bangers;
  font-size: 20px;
  background-color: silver;
  border: 1px solid grey;
  border-radius: 4px;
}
.movie-view {
  line-height: 1.5;
  padding: 2rem;
}

img {
  width: 100%;
  margin: auto 0;
}
.movie-view > div {
  padding: 4px 0;
  border-bottom: 1px dashed black;
}
.movie-title {
  margin-top: 20px;
}
.favorite-button {
  font-family: bangers;
  border-radius: 2px;
  font-size: 14px;
  line-height: 1.4;
  padding: 4px 2px;
  margin-left: 4px;
  background-color: #89BBFE;
}

.label {
  margin: 10px 0;
  font-family: alex;
  font-weight: 900;
  font-size: 1.6em;
}
.value {
  margin: 10px 0;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}
.genre-link {
  color: #fff;
}
.director-link {
  color: #fff;
}
.relMovies > * {
  text-decoration: none;
  color: #212529;
  padding: 4px 0;
}
.error-msg {
  font-size: 12px;
  width: 100%;
  color: red;
}
.success-msg {
  background-color: #0f834d;
  color: #fff;
  width: 100%;
  font-weight: 600;
  padding: 20px;
}

